.menu-top {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1011;
}

.menu-top .navbar {
  position: relative;
  padding-top: 0;
  padding-bottom: 0;
}

.menu-top .navbar .navbar-brand {
  position: absolute;
  top: 0;
  width: calc(98%);
  text-align: center;
  margin: 0;
  z-index: 0;
  padding: 0;
  padding-top: 10px;
}

.menu-top .navbar .navbar-collapse {
  flex-grow: 0 !important;
  z-index: 1;
}

.menu-top .navbar .navbar-collapse .nav-item:hover::before {
  background: #54d154;
  width: 100%;
}

.menu-top .navbar .navbar-collapse .nav-item::before {
  content: "";
  display: block;
  width: 0%;
  height: 6px;
  margin: auto;
  background: transparent;
  transition: 500ms;
}

.menu-top .navbar .navbar-collapse .nav-item.active::before {
  background: #54d154;
  width: 100%;
}

.menu-top .navbar .navbar-collapse .nav-item .nav-link {
  color: #fff;
  font-family: 'Montserrat';
  font-weight: 500;
}

.menu-top .navbar .navbar-toggler {
  z-index: 1;
  border-color: rgba(84, 209, 84, 0.1);
  color: rgba(84, 209, 84, 0.1);
}

.menu-top .navbar .navbar-toggler .navbar-toggler-icon {
  background: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(84, 209, 84, 0.5)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
}

.menu-top .navbar .searchbox {
  z-index: 1;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 20px;
  color: #1111;
  margin-left: auto;
  margin-top: 10px;
}

.menu-top .navbar .searchbox input {
  width: 155px;
  border: none;
  background: transparent;
  height: 27px;
  padding-left: 6px;
  box-shadow: none;
}

.menu-top .navbar .searchbox .fa {
  margin-right: 5px;
  font-size: 17px;
  background: transparent;
  border: none;
  color: #111;
}

header {
