  margin: auto;
}

#seen .img img {
  margin: auto;
  height: 47px;
  max-width: none;
  width: auto;
}

@keyframes rotating {
  0% {
    transform: rotate(0);
  }

  25% {
    transform: rotate(1deg);
  }

  50% {
    transform: rotate(0deg);
  }

  75% {
    transform: rotate(-1deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

body {
  min-width: 320px;
  max-width: 100vw;
  font-family: 'Montserrat';
  font-weight: 300;
  font-size: 15px;
  color: #b7b3b3;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

a:hover {
  text-decoration: none;
}

h3 {
  text-transform: uppercase;
  font-family: 'Montserrat';
  font-weight: 400;
  font-size: 24px;
  color: #828181;
}

h3 + p {
  font-size: 16px;
  color: #828181;
}

h3::after {
  content: '';
  display: block;
  width: 43px;
  height: 2px;
  background: #828181;
  margin: auto;
  margin-top: 12px;
}

h3:hover::after {
  animation: after 2s linear infinite;
}

.gbtn {
  background: #54d154;
  border-radius: 20px;
  color: #fff;
  border: none;
  font-weight: 400;
  cursor: pointer;
  display: inline-block;
  transition: 500ms ease-in-out;
}

.gbtn:hover {
  color: #54d154;
  background: #2b9b2b;
}

section {
  padding: 42px 0 52px 0;
}

.pswp {
  z-index: 1000;
}

.pswp__img {
  object-fit: cover;
}

.pswp__bg {
  background: #122412;
}

.up {
  position: fixed;
  bottom: 5%;
  right: 5%;
  width: 60px;
  height: 60px;
  border: 2px dotted #2b9b2b;
  text-align: center;
  font-size: 50px;
  line-height: 50px;
  color: #2b9b2b;
  cursor: pointer;
  z-index: 10;
  display: none;
}

@media (max-width: 992px) {
  .menu-top .navbar {
    padding-top: 8px;
    padding-bottom: 8px;
  }

  .menu-top .navbar .navbar-brand {
    width: auto;
  }

  .menu-top .navbar .navbar-brand {
    padding-left: 1rem;
  }

  .menu-top .navbar .navbar-brand {
    text-align: left;
  }

  .menu-top .navbar .navbar-collapse .nav-item {
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
  }

  .menu-top .navbar .navbar-collapse .nav-item::before {
    display: none;
  }

  .menu-top .navbar .navbar-collapse .nav-item.active {
    border-bottom: 6px solid #54d154;
  }

  .menu-top .navbar .navbar-toggler {
    margin-left: auto;
  }

  .menu-top .navbar .searchbox {
    display: none;
    margin-left: 0;
  }

  #work .gutter-sizer {
    width: 2%;
  }

  #work .wbox {
    width: 48.999%;
    margin-top: 2%;
  }

  #team .profilewrap {
    justify-content: center;
  }

  #team .profilewrap .profile:last-child {
    margin-bottom: 0;
  }

  #team .profilewrap .profile {
    flex-basis: 49%;
    margin-bottom: 50px;
  }

  .footer .col-lg-4 {
    margin-bottom: 60px;
  }
}

@media (max-width: 768px) {
  #wedo .col-lg-4 {
    margin-top: 70px;
  }

  #wedo .col-lg-4:first-child {
    margin-top: 40px;
  }

  #testim .quote .box {
    max-width: 90%;
  }
}

@media (max-width: 576px) {
  header .headerSlider .owl-dots {
    flex-direction: row;
    left: 0;
    top: 90vh;
    justify-content: center;
    width: 100%;
  }

  header .headerSlider .owl-dots .owl-dot {
    margin-bottom: 0;
    margin-right: 5px;
  }

  .up {
    width: 40px;
    height: 40px;
    font-size: 20px;
    line-height: 35px;
  }
}